
#app {
  height: 100%;
  padding-top: 60px;
}
.nav {
  width: 100%;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid rgba(56, 156, 144, .2);
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  background-color: #fff;
}
.navlink {
  display: inline-block;
  padding: 0 30px;
  border-right: 1px solid rgba(56, 156, 144, .2);
  color: #004D40;
  position: relative;
}
.navlink.router-link-active:before,
.navlink.router-link-active:after {
  width: 50%;
}
.navlink:before,
.navlink:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -1px;
  width: 0;
  height: 2px;
  background-color: #004D40;
  transition: width .2s;
}
.navlink:before {
  right: 50%;
}
.navlink:after {
  left: 50%;
}
.navlink:hover:before,
.navlink:hover:after {
  width: 50%;
}
.mobile-nav {
  position: fixed;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  top: 0;
  right: 60px;
  display: none;
  font-size: 20px;
  color: #00AA90;
  z-index: 9;
}
.mobile-nav-link {
  position: fixed;
  top: 0;
  right: -100%;
  width: 60%;
  height: 100%;
  z-index: 5;
  background-color: #fff;
  box-shadow: 0 -5px 20px rgba(0, 0, 0, .3);
  transition: all .2s;
}
.nav-show {
  right: 0;
}
.mobile-nav-link li {
  border-bottom: 1px solid #eee;
}
@media screen and (max-width: 800px) {
.navlink {
    display: none;
}
.mobile-nav {
    display: block;
}
}
.logo {
  display: inline-block;
  width: 200px;
  text-align: center;
  background-color: #004D40;
  color: #fff;
  font-size: 17px;
  position: relative;
}
.logo:before {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #004D40;
  position: absolute;
  left: 0;
  bottom: -1px;
}
.view {
  height: 100%;
  overflow: auto;
}
h1 {
  font-size: 28px;
  text-align: center;
  color: #00695C;
  margin: 20px auto;
  font-weight: normal;
}
h2 {
  font-size: 20px;
  font-weight: normal;
  margin: 20px 0;
}
@media screen and (max-width: 420px) {
h1 {
    font-size: 24px;
}
h2 {
    font-size: 18px;
}
}
.wrap {
  width: 800px;
  padding: 0 20px;
  margin: 0 auto 50px;
}
.mb20 {
  margin-bottom: 20px;
}
@media screen and (max-width: 800px) {
.wrap {
    width: 100%;
    padding: 0 20px;
}
.banner {
    display: none;
}
}
.row {
  margin-bottom: 5px;
  position: relative;
  padding-left: 20px;
}
.tip {
  position: absolute;
  top: 4px;
  right: 0;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background-color: #00AA90;
  color: #fff;
  transition: all .2s;
  z-index: 2;
  overflow: hidden;
}
.tip:hover {
  width: 400px;
  height: 110px;
  border-radius: 6px;
  z-index: 3;
}
@media screen and (max-width: 420px) {
.tip:hover {
    width: 300px;
}
}
.tip:hover .tip-content {
  opacity: 1;
}
.tip-content {
  height: 100%;
  padding-left: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-right: 30px;
  overflow-y: auto;
  font-size: 13px;
  list-style-type: circle;
  opacity: 0;
  transition: all .2s;
}
.tip-content::-webkit-scrollbar-thumb {
  background-color: #00AA90; 
  border-radius: 3px;
}
.fa-bell {
  font-size: 10px;
  position: absolute;
  top: 5px;
  right: 5px;
}
.sub-nav {
  text-align: center;
  margin-bottom: 40px;
}
.sub-nav a {
  color: #ccc;
  padding: 0 20px;
}
.sub-nav a.router-link-active {
  color: #00AA90;
}
.fa-bookmark {
  margin-right: 6px;
  color: #00AA90;
}
.fa-anchor {
  color: inherit;
}
.contribute {
  position: fixed;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  top: 0;
  right: 0;
  font-size: 20px;
  color: #00695C;
  z-index: 9;
  cursor: pointer;
}
.fa-weibo,
.fa-github {
  font-size: 24px;
  margin-top: 10px;
}
.fa-weibo {
  margin-right: 10px;
}


/* -------------- basic reset -------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  height: 100%;
}
body {
  height: 100%;
  font-size: 15px;
  line-height: 1.8;
  font-family: 'helvetica neue', 'hiragino sans gb', stheiti, 'wenquanyi micro hei', \5FAE\8F6F\96C5\9ED1, \5B8B\4F53, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}
@media screen and (max-width: 360px) {
body {
    font-size: 13px;
}
}
ul {
  list-style-type: none;
  list-style-image: none;
}
a {
  text-decoration: none;
  background-color: transparent;
}
a:active,
a:hover {
  outline-width: 0;
}
code,
pre {
  font-family: monaco, menlo, consolas, 'courier new', courier, monospace;
}
pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: auto;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-thumb {
  background-color: #ddd; 
  border-radius: 3px;
}
::-moz-selection {
  color: #fff;
  background: #389C90;
}
::selection {
  color: #fff;
  background: #389C90;
}
.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  float: left;
}
.col-12 {
  width: 100%;
}
.col-11 {
  width: 91.66666667%;
}
.col-10 {
  width: 83.33333333%;
}
.col-9 {
  width: 75%;
}
.col-8 {
  width: 66.66666667%;
}
.col-7 {
  width: 58.33333333%;
}
.col-6 {
  width: 50%;
}
.col-5 {
  width: 41.66666667%;
}
.col-4 {
  width: 33.33333333%;
}
.col-3 {
  width: 25%;
}
.col-2 {
  width: 16.66666667%;
}
.col-1 {
  width: 8.33333333%;
}



